import {
  EmailInput,
  PasswordInput,
  Input,
  Button,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { useState, useEffect, FormEvent } from "react";
import styles from "./profile.module.css";
import { Link } from "react-router-dom";
import { useProvideAuth } from "../../components/UseAuth/useAuth";
import { userInfoRequest, userInfoChangeRequest } from "../../api/api";
import { useDispatch, useSelector } from "../../services/types/hooks";
import { REGISTRATION_SET_DATA } from "../../services/actions/registration";
import Loader from "../../components/Loader/Loader";
import handleChange from "../../utils/handleChange";

export default function Profile() {
  const [name, setName] = useState<string>("");
  const [email, setEmail] = useState<string>("");
  const [password, setPassword] = useState<string>("");
  const [isEdited, setIsEdited] = useState<boolean>(false);

  const dispatch = useDispatch();

  const userData = useSelector((store) => store.regisrationReducer.user);

  async function getData() {
    const user = await userInfoRequest();
    if (user) {
      dispatch({ type: REGISTRATION_SET_DATA, user: user });
      setName(user.name);
      setEmail(user.email);
    }
  }

  useEffect(() => {
    getData();
  }, []);

  function reset() {
    if (userData) {
      setName(userData.name);
      setEmail(userData.email);
      setIsEdited(false);
    }
  }

  async function changeData(e: FormEvent<HTMLFormElement>) {
    e.preventDefault();
    const user = await userInfoChangeRequest({ name: name, email: email });
    if (user) {
      dispatch({ type: REGISTRATION_SET_DATA, user: user });
      setName(user.name);
      setEmail(user.email);
      setIsEdited(false);
    }
  }

  const { signOut } = useProvideAuth();

  async function logout() {
    await signOut();
  }

  return (
    <>
      <div className={styles.container + " pl-5 pr-5"}>
        <div className={styles.navigation}>
          <Link
            to="/profile"
            className={
              styles.link +
              " text text_type_main-medium pt-4 pb-4 " +
              styles.whiteText
            }
          >
            
          </Link>
          <Link
            to="orders"
            className={
              styles.link +
              " text text_type_main-medium text_color_inactive pt-4 pb-4"
            }
          >
             
          </Link>

          <Link
            to="#"
            className={
              styles.link +
              " text text_type_main-medium text_color_inactive pt-4 pb-4"
            }
            onClick={(e) => logout()}
          >
            
          </Link>

          <p
            className={
              styles.text +
              " text text_type_main-default text_color_inactive mt-20"
            }
          >
                    
          </p>
        </div>

        <form
          className={styles.form + " mb-20"}
          onSubmit={(e) => changeData(e)}
        >
          {name ? (
            <>
              <Input
                type={"text"}
                placeholder=""
                value={name}
                onChange={(e) => {
                  handleChange(e, setName);
                  setIsEdited(true);
                }}
                name={"name"}
                error={false}
                errorText={""}
                size={"default"}
                icon="EditIcon"
              />
              <EmailInput
                onChange={(e) => {
                  handleChange(e, setEmail);
                  setIsEdited(true);
                }}
                value={email}
                name={"email"}
                placeholder=""
                isIcon={true}
                extraClass="mt-6"
              />
              <PasswordInput
                onChange={(e) => {
                  handleChange(e, setPassword);
                  setIsEdited(true);
                }}
                value={password}
                name={"password"}
                icon="EditIcon"
                extraClass="mt-6"
              />
              {isEdited && (
                <div className="mt-6">
                  <Button
                    type="secondary"
                    size="medium"
                    htmlType="reset"
                    onClick={reset}
                  >
                    
                  </Button>
                  <Button type="primary" size="medium" htmlType="submit">
                    
                  </Button>
                </div>
              )}
            </>
          ) : (
            <Loader />
          )}
        </form>
      </div>
    </>
  );
}
